@charset "UTF-8";
:root {
  --clip: left center;
  --size: 0;
  --background-thumb: #2b3769;
  --scrollbar-thumb-bgcolor: #757575;
  --stripe-width: 10px;
  --nav-marker-border: #3b8686;
  --sidebar-width: 28.5rem;
  --duration: 0.3s;
  --button-size: 4rem;
  --mob-expanded-width: 320px;
  --mob-collapsed-width: 4rem;
}

.masked-input {
  color: transparent; /* Скрыть текст, пока маска не активна */
}

.masked-input::placeholder {
  color: black; /* Цвет плейсхолдера */
}

.gradient-preloader {
  background: linear-gradient(90deg, #2b3769 -23.739%, #93a9ff 96.778%);
}

@media (min-width: 1440.99px) {
  .underline:hover {
    font-weight: 600;
    opacity: 1;
  }
  .underline:hover::before {
    width: 47%;
  }
  .underline:hover::after {
    width: 47%;
  }
  .underline::before {
    content: "";
    position: absolute;
    right: 0;
    bottom: 0px;
    width: 0;
    height: 2px;
    background-color: #000;
    opacity: 0.8;
    transition: width 0.4s ease-in-out;
  }
  .underline::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0px;
    width: 0;
    height: 2px;
    background-color: #000;
    opacity: 0.8;
    transition: width 0.4s ease-in-out;
  }
  .underline-w:hover {
    font-weight: 600;
    opacity: 1;
  }
  .underline-w:hover::before {
    width: 47%;
  }
  .underline-w:hover::after {
    width: 47%;
  }
  .underline-w::before {
    content: "";
    position: absolute;
    right: 0;
    bottom: 0px;
    width: 0;
    height: 2px;
    background-color: #fff;
    opacity: 0.8;
    transition: width 0.4s ease-in-out;
  }
  .underline-w::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0px;
    width: 0;
    height: 2px;
    background-color: #fff;
    opacity: 0.8;
    transition: width 0.4s ease-in-out;
  }
}
@media screen and (max-width: 1140px) {
  .project {
    background: #2b3769;
  }
}
.project__title {
  text-align: center;
  margin-bottom: 53px;
}
@media (min-width: 960.98px) and (max-width: 1920px) {
  .project__title {
    font-size: calc(24px + 41 * (100vw - 960px) / 960);
  }
}
@media screen and (max-width: 1140px) {
  .project__title {
    color: #fff;
    margin-bottom: 40px;
  }
}
.project__content {
  position: relative;
  z-index: 2;
  background: #2b3769;
  padding-top: 50px;
  padding-bottom: 50px;
}
@media (min-width: 490.99px) {
  .project__content {
    background-image: radial-gradient(circle at 15% 125%, rgba(160, 176, 240, 0.637) 0%, rgb(43, 55, 105) 40%, transparent 25%), radial-gradient(circle at 99% 10%, rgba(160, 176, 240, 0.637) 0%, rgb(43, 55, 105) 40%, transparent 25%);
  }
}
@media screen and (max-width: 490px) {
  .project__content {
    background-image: radial-gradient(ellipse at 90% 25%, rgba(160, 176, 240, 0.637) 0%, rgba(238, 238, 238, 0.048) 20%, transparent 75%), radial-gradient(circle at 10% 99%, rgba(160, 176, 240, 0.637) 0%, rgba(43, 55, 105, 0.891) 7%, transparent 25%);
  }
}
@media (min-width: 960.99px) {
  .project__content {
    border-radius: 30px;
  }
}
@media screen and (max-width: 1440px) {
  .project__content {
    padding: 50px 30px;
  }
}
@media screen and (max-width: 1140px) {
  .project__content {
    border-radius: 0px;
  }
}
@media screen and (max-width: 690px) {
  .project__content {
    padding: 50px 30px 60px;
  }
}
@media screen and (max-width: 375px) {
  .project__content {
    padding: 50px 10px 60px;
  }
}
.project__tabs {
  position: relative;
  width: 100%;
  display: grid;
  align-items: center;
  justify-content: space-between;
  grid-template-columns: 1fr min-content;
  transition: all 0.7s ease 0s;
}
.project__tabs._responsive {
  background: #3a4a91;
  grid-template-rows: 1fr;
  height: 100%;
}
@media screen and (max-width: 960px) {
  .project__tabs._responsive .tabs-button__item {
    position: relative;
    opacity: 1;
    pointer-events: all;
    transform: translateX(0);
  }
  .project__tabs._responsive .tabs-button__item:nth-child(4) {
    padding-bottom: 15px;
  }
}
@media (min-width: 1140.99px) {
  .project__tabs {
    margin-bottom: -30px;
    height: 145px;
  }
}
@media (min-width: 1140.98px) and (max-width: 1920px) {
  .project__tabs {
    height: calc(108px + 37 * (100vw - 1140px) / 780);
  }
}
@media screen and (max-width: 1140px) {
  .project__tabs {
    border-top-left-radius: 0px;
    border-top-right-radius: 0px;
    background: #2b3769;
    padding: 0 25px 0 30px;
  }
}
@media screen and (max-width: 960px) {
  .project__tabs {
    grid-template-rows: 0fr;
    overflow: hidden;
  }
}
@media screen and (max-width: 375px) {
  .project__tabs {
    padding: 0 5px 0 10px;
  }
}
.project__footer {
  display: flex;
  justify-content: center;
}
.project__button {
  background: #ffffff;
}
.project__button .icon-arrow-dow {
  color: #fff;
}
.project__button:hover {
  border: 1px solid #fff;
}
.project__button:hover .icon {
  transform: rotate(-90deg);
}
.project__button:hover span {
  color: #fff;
}
.project__button:hover::before {
  background: #2b3769;
}
.project__icon .icon-arrow-down::before {
  color: #fff;
}

.tabs-button__wrapper {
  width: 100%;
  height: 100%;
  min-height: 60px;
  display: flex;
  align-items: flex-end;
  gap: 10px;
}
@media screen and (max-width: 1140px) {
  .tabs-button__wrapper {
    padding: 20px 0;
    align-items: center;
    box-sizing: border-box;
    justify-content: flex-start;
    gap: 40px;
  }
}
@media screen and (max-width: 960px) {
  .tabs-button__wrapper {
    align-items: flex-start;
    flex-direction: column;
    padding: 0;
    gap: 0;
  }
}
.tabs-button__item {
  cursor: pointer;
  font-weight: 400;
  line-height: 120%; /* 42px/35 */
  white-space: nowrap;
  color: #fff;
  display: flex;
  align-items: flex-start;
  border-top-left-radius: 30px;
  border-top-right-radius: 30px;
}
.tabs-button__item span {
  position: relative;
  display: inline-flex;
  line-height: 150%;
}
@media screen and (max-width: 1440px) {
  .tabs-button__item span {
    font-size: 24px;
    padding: 0 30px;
  }
}
@media screen and (max-width: 1140px) {
  .tabs-button__item span {
    padding: 0px;
  }
  .tabs-button__item span::after {
    content: "";
    position: absolute;
    bottom: -2px;
    width: 100%;
    height: 2px;
    opacity: 0;
    transition: opacity 0.4s ease-in-out;
  }
}
@media screen and (max-width: 960px) {
  .tabs-button__item span {
    margin: 10px 0;
    font-size: 20px;
  }
}
.tabs-button__item:hover {
  box-shadow: 0 -1px 7px #b8b8b8;
}
@media (min-width: 1140.99px) {
  .tabs-button__item:not(.active) {
    color: #000;
    background: #f2f2f2;
    height: 128px;
  }
}
@media (min-width: 1140.98px) and (max-width: 1920px) {
  .tabs-button__item:not(.active) {
    height: calc(80px + 48 * (100vw - 1140px) / 780);
  }
}
@media screen and (max-width: 960px) {
  .tabs-button__item:not(.active) {
    pointer-events: none;
    transform: translateX(-100%);
    opacity: 0;
  }
}
.tabs-button__item.active {
  box-shadow: none;
  height: 100%;
  background: #2b3769;
}
@media screen and (max-width: 960px) {
  .tabs-button__item.active {
    background: transparent;
    position: absolute;
  }
}
@media (min-width: 1140.98px) and (max-width: 1920px) {
  .tabs-button__item.active {
    height: calc(100px + 45 * (100vw - 1140px) / 780);
  }
}
@media screen and (max-width: 1140px) {
  .tabs-button__item.active span::after {
    opacity: 0.5;
    background: #fff;
  }
}
@media (min-width: 1140.99px) {
  .tabs-button__item:nth-child(1) span {
    display: inline-flex;
    text-align: center;
  }
}
@media screen and (max-width: 960px) {
  .tabs-button__item:nth-child(1) {
    max-width: 100%;
  }
}
@media (min-width: 1140.99px) {
  .tabs-button__item {
    padding: 15px 0 40px;
    transition: all 0.4s ease-in-out;
  }
}
@media screen and (max-width: 960px) {
  .tabs-button__item {
    width: 260px;
    z-index: 1;
  }
}
@media (min-width: 960.99px) {
  .tabs-button__button {
    display: none;
  }
}
@media screen and (max-width: 960px) {
  .tabs-button__button {
    position: relative;
    display: block;
    align-self: start;
    margin: 10px 0;
    z-index: 2;
  }
  .tabs-button__button .burger-button__items span {
    background: #fff;
  }
  .tabs-button__button .burger-button__items__wrapper {
    background: transparent;
  }
}

.tab-content {
  position: relative;
  height: 100%;
}
.tab-content__items {
  display: none;
  margin-bottom: 40px;
  grid-template-columns: repeat(3, 1fr);
  gap: 40px;
}
@media (min-width: 1440.98px) and (max-width: 1920px) {
  .tab-content__items {
    gap: calc(20px + 20 * (100vw - 1440px) / 480);
  }
}
@media screen and (max-width: 1440px) {
  .tab-content__items {
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
  }
}
@media screen and (max-width: 690px) {
  .tab-content__items {
    grid-template-columns: repeat(1, 1fr);
  }
}
.tab-content__items span {
  display: inline;
}
.tab-content__items.active {
  display: grid !important;
}
.tab-content__column {
  cursor: pointer;
  position: relative;
  display: flex;
  flex-direction: column;
}
@media (min-width: 960.99px) {
  .tab-content__column:hover .tab-content__text {
    opacity: 1;
    scale: 1;
  }
}
.tab-content__image {
  cursor: pointer;
  width: 100%;
  overflow: hidden;
  border-radius: 30px;
  overflow: hidden;
  margin-bottom: 5px;
}
.tab-content__image img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}
@media screen and (max-width: 1140px) {
  .tab-content__image {
    border-radius: 15px;
  }
}
@media (min-width: 768.98px) {
  .tab-content__image {
    pointer-events: none;
  }
}
@media screen and (max-width: 768px) {
  .tab-content__image {
    margin-bottom: 5px;
    border-radius: 15px;
  }
}
.tab-content__footer {
  flex: 1 1 auto;
  display: flex;
  align-items: center;
  gap: 10px;
  justify-content: space-between;
}
@media (min-width: 1440.98px) and (max-width: 1740px) {
  .tab-content__footer {
    flex-direction: column;
    gap: 20px;
  }
}
@media screen and (max-width: 1164px) {
  .tab-content__footer {
    flex-direction: column;
    gap: 20px;
  }
}
.tab-content__text {
  pointer-events: none;
  color: #fff;
  line-height: 125%; /* 20px/16 */
  text-align: left;
  flex: 1 1 auto;
  display: none;
  border-radius: 30px;
}
@media screen and (max-width: 1164px) {
  .tab-content__text {
    max-width: 100%;
    width: 100%;
  }
}
@media (min-width: 960.99px) {
  .tab-content__text {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    padding: 30px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-end;
    background: rgba(60, 59, 81, 0.8078431373);
    scale: 0;
    opacity: 0;
    transition: opacity 0.4s, scale 0.4s ease 0s;
  }
  .tab-content__text span {
    height: 100%;
    display: flex;
    align-items: center;
  }
}
@media screen and (max-width: 960px) {
  .tab-content__text {
    display: block;
    position: relative;
  }
}
.tab-content__button {
  right: 0;
  bottom: 0;
  background: #2b3769;
  border: 1px solid transparent;
}
.tab-content__button span {
  color: #fff;
}
.tab-content__button:hover .icon {
  transform: rotate(-45deg);
  background: #2b3769;
}
.tab-content__button:hover ._icon::before {
  color: #fff;
  font-weight: 400;
}
.tab-content__button:hover span {
  color: #000;
}
.tab-content__button:hover::before {
  background: #fff;
  color: #fff;
}
.tab-content__button .icon {
  background: #fff;
}
.tab-content__button ._icon {
  border-radius: 50%;
  color: #2b3769;
}
.tab-content__button ._icon::before {
  font-weight: 600;
}
@media screen and (max-width: 960px) {
  .tab-content__button {
    width: 268px;
  }
}
.tab-content__icon {
  flex: 0 0 4.8rem;
  pointer-events: all;
  width: 4.8rem;
  height: 4.8rem;
  border-radius: 50%;
  background: #fff;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease-in-out;
  overflow: hidden;
  display: grid;
  grid-template-columns: 1fr;
}
.tab-content__icon:hover {
  box-shadow: 1px 1px 7px #000000;
}
.tab-content__icon:hover .icon-arrow-right {
  transform: translateY(-100%);
}
.tab-content__icon:hover .icon-group {
  transform: translateY(-100%);
}
@media screen and (max-width: 960px) {
  .tab-content__icon {
    display: none;
  }
}
.tab-content__icon ._icon {
  transition: all 0.5s ease 0s;
  height: 4.8rem;
}
.tab-content__icon ._icon::before {
  color: #2b3769;
  font-weight: 900;
}
.tab-content__icon .icon-group::before {
  font-size: 14px;
}